.adContainer {
  @add-mixin cover;
}

.hasAd {
  /* Fully occlude all elements stacked below the ad container. */
  background-color: #000;
}

.hasNoAd {
  /*
  IMA leaves the IFRAME mounted as soon as the display container is created even
  though IMA has no way to control the IFRAME until the adsManager is created.
  So, instead of destructing the container we hide it to ensure it doesn't cover
  playback controls.
  See: https://github.com/plexinc/plex-web-client/issues/16514
  */
  display: none;
}
